<template lang="html">
  <div id="care">
    <header-bar>
      <div slot="care" class="care-header-bar">
        <span>我关注的</span>
        <Icon type="person-add" size="28" class="fr addFriends" color="#fff"></Icon>
      </div>
    </header-bar>
    <div class="no">
      <div class="con">
        因数据限制，暂未开发，后续可能更换此频道内容
      </div>
    </div>
    <bottom-nav></bottom-nav>
  </div>
</template>

<script>
import bottomNav from '../components/Bottom-nav.vue'
import headerBar from '../components/Header-bar.vue'
export default {
  components: {
    bottomNav,
    headerBar
  }
}
</script>

<style lang="less">
#care {
  .care-header-bar {
    font-size: 16px;
    text-align: center;
    color: #fff;
    span {
      margin-right: -1rem;
    }
    .addFriends {
      line-height: 1.2rem;
      margin-right: 0.3rem;
    }
  }
  .no {
    position: relative;
    top: 2rem;
    font-size: 30px;
    .con {
      width: 94%;
      margin: 0 auto;
    }
  }
}
</style>



