<template>
  <div id="selfpage">
    <headerBar>
      <div slot="selfpage">
        <Icon type="ios-arrow-back" size="28" color="#fff" class="fl back" @click.native="$router.go(-1)"></Icon>
        <span class="my-selfpage">我的主页</span>
        <Icon type="share" size="28" color="#fff" class="fr share" @click.native="showSharebox"></Icon>
      </div>
    </headerBar>
    <div class="self-info clearfix">
      <div class="leftinfo fl"></div>
      <div class="rightWriter fr">
        <router-link to="/editprofile">编辑资料</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import headerBar from '../components/Header-bar.vue'
export default {
  components: {
    headerBar
  }
}
</script>

<style lang="less">
#selfpage {
  .back {
    line-height: 1.2rem;
    margin-left: 0.3rem;
  }
  .share {
    line-height: 1.2rem;
    margin-right: 0.3rem;
  }
  .my-selfpage {
    font-size: 18px;
    font-weight: bold;
    color: #fff;
  }
}
</style>



